@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(197, 222, 223);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (max-width: 1700px) {
  html {
    font-size: 90%;
  }
}

@media screen and (max-width: 1500px) {
  html {
    font-size: 85%;
  }
}

@media screen and (max-width: 1400px) {
  html {
    font-size: 80%;
  }
}

@media screen and (max-width: 1300px) {
  html {
    font-size: 75%;
  }
}

main {
  font-family: "Poppins", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 20vh;
  padding-bottom: 20vh;
  width: 100%;
}

header {
  padding: 0.5rem 1.6rem;
  margin-bottom: 4rem;
  font-size: 5rem;
  text-transform: uppercase;
  letter-spacing: 1rem;
  border: 0.5rem solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-section {
  padding: 4rem 0rem;
  width: 100rem;
  background: white;
}

.main-section .main-container .main-title {
  margin: 0 2rem;
  margin-bottom: 3rem;
  text-transform: uppercase;
  letter-spacing: 0.4rem;
  text-align: center;
  font-size: 6rem;
  font-weight: 700;
}

.main-section .main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.main-section .main-container h2 {
  margin: 0 2rem;
  margin-bottom: 3rem;
  font-size: 1.6rem;
  text-align: center;
  -webkit-transition: color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out;
  color: rgb(100, 100, 100);
}

.main-section .main-container h2:hover {
  color: rgb(50, 200, 200);
}
.main-section .common-content {
  width: 80%;
  line-height: 2.5rem;
  font-size: 1.3rem;
  color: rgb(100, 100, 100);
}

.main-section .main-figure {
  width: 80%;
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
  overflow: hidden;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.main-section .main-figure .main-img {
  width: 100%;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.main-section .main-figure:hover .main-img {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.main-section .main-container .item-block-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 6rem 0;
  border-top: 0.14rem solid rgb(230, 230, 230);
  border-bottom: 0.14rem solid rgb(230, 230, 230);
}

@media screen and (max-width: 1300px) {
  .main-section {
    width: 90%;
  }
}

@media screen and (max-width: 1000px) {
  .main-section .main-container .item-block-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
    margin: 6rem 0;
    border-top: 0.14rem solid rgb(230, 230, 230);
    border-bottom: 0.14rem solid rgb(230, 230, 230);
  }

  nav {
    display: none;
  }
}

@media screen and (max-width: 714px) {
  .main-section {
    width: 100%;
  }

  .main-section .main-container .main-title {
    font-size: 4rem;
  }
}
nav {
    width: 100rem;
    height: 6rem;
    background: rgba(255, 255, 255, 0.33);
}

.nav-links {
    display: flex;
    justify-content:space-around;
    font-size: 1.3rem;
    letter-spacing: 0.4rem;
    width: 50rem;
    height: 100%;
}

.nav-links .link {
    text-decoration: none;
    color: black;
    background: rgba(255, 255, 255, 0);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    outline: none;
}
.link.active {
    background: white;
}

.nav-links .link:hover {
    background: rgba(255, 255, 255, 0.5);
}

.link.active:hover {
    color: rgb(50, 200, 200);
    background: white;
}

@media screen and (max-width: 1300px) {
    nav {
      width: 90%;
    }
  }
.item-block {
  padding: 4rem 0rem;
  width: 50%;
  border-bottom: 0.14rem solid rgb(230, 230, 230);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item-block.right {
  border-right: 0.14rem solid rgb(230, 230, 230);
}

.item-block.odd {
  border-right: 0;
}

.item-block.last {
  border-bottom: 0;
}

.item-block p {
  font-size: 1.3rem;
  color: rgb(100, 100, 100);
  line-height: 2.5rem;
  width: 80%;
}

.item-block .item-figure {
  width: 80%;
  display: flex;
  justify-content: center;
  height: 20vw;
  margin-bottom: 3rem;
  overflow: hidden;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.item-block .item-figure .item-img {
  width: 100%;
  height: auto;
  object-fit: cover;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.item-block .item-figure:hover .item-img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media screen and (max-width: 1000px) {
  .item-block .item-figure {
    width: 60%;
    height: 30vw;
  }
  .item-block {
    width: 100%;
  }
  .item-block.right {
    border-right: 0;
  }

  .item-block.odd {
    border-right: 0;
  }
}

@media screen and (max-width: 714px) {
  .item-block .item-figure {
    width: 80%;
    height: 40vw;
  }
}

